.container {
  @apply flex justify-between items-center p-3 border-b border-gray-600 bg-gray-800 h-[7vh] box-border sticky top-0 z-10;

  .linkContainer {
    @apply flex justify-between items-center w-56 mx-auto my-0;

    .link {
      @apply text-slate-200 duration-150 hover:text-blue-400;
    }
  }
}
