.questionsContainer {
  @apply flex flex-col w-full bg-gray-800 p-4 rounded-lg gap-5;

  .text {
    @apply font-bold;
  }

  .questionTitle {
    @apply text-lg;
  }

  .answersContainer {
    @apply flex flex-col;

    .answerContainer {
      @apply flex items-center gap-3;

      .input {
        @apply w-4 h-4 text-blue-600 focus:ring-blue-600 ring-offset-gray-800 focus:ring-2 bg-gray-700 border-gray-600 checked:bg-blue-600 checked:border-blue-600 cursor-pointer;
      }

      .label {
        @apply text-base cursor-pointer;
      }
    }
  }

  .buttonContainer {
    @apply min-w-52 w-auto mx-auto my-0;
  }
}
